header{
    height: 100px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: #0A323C;
    nav ul{
        display: flex;
        flex-direction: row;
        color: white;
        font-size: 25px;
        list-style-type: none;
        text-decoration: none;
        margin-right: 30px;
        a{
            text-decoration: none;

        }
        li{
            padding: 20px;
        }
        a:visited{
            color: white;
            text-decoration: none;

        }

    }
    .logo_jeu{
        height: 100px;
    }
}