.boutonFiltrePdv {
  display: inline-block;
  border: 2px solid #785A28;
  padding: 4px;
  color: #F0E6D2;
  font-weight: bold;
  background: linear-gradient(#005A82, #0AC8B9);
}

header {
  height: 100px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: #0A323C;
}
header nav ul {
  display: flex;
  flex-direction: row;
  color: white;
  font-size: 25px;
  list-style-type: none;
  text-decoration: none;
  margin-right: 30px;
}
header nav ul a {
  text-decoration: none;
}
header nav ul li {
  padding: 20px;
}
header nav ul a:visited {
  color: white;
  text-decoration: none;
}
header .logo_jeu {
  height: 100px;
}

footer {
  height: 150px;
  background-color: #1E282D;
  font-size: 30px;
  color: #C89B3C;
  display: flex;
  justify-content: center;
  align-items: center;
}

.champsZone {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}
.champsZone a {
  margin: 10px;
  height: 200px;
  width: 150px;
  border: 4px solid #785A28;
  border-radius: 10px;
  display: flex;
  color: #005A82;
  text-decoration: none;
  flex-direction: column;
}
.champsZone a h2 {
  font-size: 20px;
  text-align: center;
  margin: 5px;
}
.champsZone a:visited {
  color: #005A82;
  text-decoration: none;
}

.mainHomepage {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.mainHomepage h1 img {
  width: 40%;
  display: block;
  margin: auto;
}
.mainHomepage article {
  width: 60%;
  border: 4px solid #C89B3C;
  background-color: #091428;
  text-align: center;
  margin-bottom: 50px;
}
.mainHomepage article h2 {
  color: #C89B3C;
  font-size: 40px;
}
.mainHomepage article p {
  color: #F0E6D2;
  font-size: 30px;
}

.mainChampions {
  background-color: #F0E6D2;
}
.mainChampions h1 {
  padding: 20px;
  font-size: 40px;
  text-align: center;
}